.feedback__control {
  display: block;
  position: relative;
  padding-left: 47px;
  cursor: pointer;
}

/* Скрыть дефолтную радио кнопку */
.feedback__control-input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  left: 12px;
  top: 3px;
}

.feedback__control-mark {
  position: absolute;
  top: 0;
  left: 0;
  height: 46px;
  width: 46px;
  background-color: #f2f2f2;
  margin-top: -10px;
}
.feedback__control-mark:hover {
  background-color: #e5e5e5;
}
.feedback__control-mark:active {
  background-color: #d5d5d5;
}
.feedback__control-mark--radio {
  border-radius: 50%;
}
.feedback__control-mark--checkbox {
  border-radius: 4px;
}

/* создать новую отметку (скрытую) */
.feedback__control-mark::after {
  content: "";
  position: absolute;
  display: none;
}

.feedback__control-mark--tick {
  display: none;
  position: absolute;
  width: 32px;
  height: 32px;
  top: -6px;
  left: 8px;
}

/* Показать отметку */
.feedback__control input:checked ~ .feedback__control-mark::after {
  display: block;
}

.feedback__control input:checked ~ .feedback__control-mark--tick {
  display: block;
}

/* стили для новой отметки */
.feedback__control--radio .feedback__control-mark::after {
  top: 15px;
  left: 15px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #333333;
}

