}

.modal__confirm {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: 0;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background-color: #81b3d2;
  border: none;
}
.modal__confirm--error {
  margin-bottom: 35px;
  padding: 18px 78px 14px;
}
.modal__confirm--ok {
  margin-bottom: 19px;
  padding: 18px 75px 15px;
}
@media (min-width: 768px) {
  .modal__confirm {
    font-size: 21px;
  }
  .modal__confirm--ok {
    padding: 18px 155px 15px;
    margin-bottom: 43px;
  }
  .modal__confirm--error {
    padding: 18px 122px 14px;
    margin-bottom: 49px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  display: block;
}